*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}


.dark{
	--main_background: black;
	--main_color: white;
	--header_background: gray;
	--combo_background: #333;
	--combo_background-shadow: black;
}

.light{
	--main_background: white;
	--main_color: black;
	--header_background: rgba(100, 100, 200,1);
	--combo_background: rgba(200, 200, 255, 1);
	--combo_background-shadow: rgb(170, 170, 200);
}

body{
	background: var(--main_background);
	color: var(--main_color);
}

.combo{
	padding: 10px;
	border: 2px dashed var(--header_background);
	border-radius: 5px;
	background: var(--combo_background);	
	display: flex;
	flex-flow: column nowrap;
	justify-content: space-between;
	align-items: center;
	margin: 30px;
	height: 220px;
	box-shadow: 0 0 8px 5px var(--combo_background_shadow);
}

.combo_head{
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.combo h1{
	font-size: 3rem;
}

.combo .description{
	display: none;
	font-size: 1.3rem;
}

.combo img{
	height: 200px;
	width: auto;
	box-shadow: 0 0 10px 2px rgba(0, 0, 0, .7);
	margin: 0 0 0 10px;
	border-radius: 5px;
}

.combo.show{
	height: 400px;
}
.combo.show img{
	height: 350px;
	margin: 20px 0;
}
.combo.show h1{
	font-size: 4rem;
}
.combo.show .description{
	display: block;
}

header{
	width: 100vw;
	text-align: center;
	padding: 7px;
	background: var(--header_background);
}

header input{
	outline: none;
	border-radius: 2px;
  width: 50%;
  padding: 4px 10px;
  font-size: 1.05rem;
  border: none;
  background: white;
  transition: .1s;
}

header button{
	float: right;
	padding: 4px;
}

header input:focus{
 outline: 2px solid #67d;
 background: var(--combo_background);
 color: inherit;
}


@media(max-width: 760px){
	.combo{
		margin: 30px 0;
	}	
	.combo.show .combo_head{
		flex-flow: column nowrap;
	}
	.combo.show{
		height: auto;
	}
	.combo h1{font-size: 2.5rem}
}

.name_combo{
	word-break: break-all
	/*hyphens: auto;*/
	/*overflow-wrap: break-word;*/
}
